.ul {
    color: #264653;
    font-size: 18px;

    &__button-ul {
        display: block;
        padding: 10px 15px;
        border-style: none;
        background-color: #f4a261;
        color: #fff;
        font-size: 18px;
        cursor: pointer;
        outline: none;

        &:hover {
            background-color: #e76f51;
        }
    }
}